.tic-tac {
  width: 300px;
  height: 300px;
  border: 2px solid black;
  text-align: center;
  vertical-align: middle;
  font-size: 65px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  background-color: rgb(240, 239, 222);
  color: rgb(0, 4, 231);
  }
  .ttt-one{
    width: 100px;
    height: 100px;
    border: 1px solid  black;
    float: left;
    box-sizing: border-box;
    

  }